import React from 'react';
import Favorites from '../Favorites';
import LKHome from '../LKHome';
import Registration from '../Registration';
import SignIn from '../SignIn';

type LKContentTypes = {
	action: string | string[];
};

const LKContent = ({ action }: LKContentTypes) => {
	if (action === 'sign-in') {
		return <SignIn />;
	} else if (action === 'registration') {
		return <Registration />;
	} else if (action === 'favorites') {
		return <Favorites />;
	} else if (action === 'lk') {
		return <LKHome />;
	}
};

export default React.memo(LKContent);
