import Title from 'components/UI/Title';

import Image from 'next/image';
import React from 'react';
import arrowDark from 'public/static/img/arrowDark.svg';

import styles from './LKHome.module.scss';
import Button from 'components/UI/Button';
import LKSettings from '../LKSettings';
import { useRouter } from 'next/router';
import routes from 'helpers/routes';
import LKResetPass from '../LKResetPass';
import LKOrders from '../LKOrders';
import { useSelector } from 'react-redux';
import { SuisseIntl } from 'helpers/utils/fonts';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import {
	changeBirthday,
	changeCity,
	changeEmail,
	changeFirstName,
	changeId,
	changeLastName,
	changeMiddleName,
	changePhone,
	changeToken,
} from 'store/user/UserDataStore';
import PaymentAndDelivery from '../PaymentAndDelivery';
import { deleteCookie } from 'cookies-next';
import { getCheckoutProduct } from 'store/product/CheckoutProductStore';
import { getFavoritesIds } from 'store/favorites/FavoritesStore';
import { changeOrderList, changePage, changeTotalPages, getOrderList } from 'store/order/OrderListStore';

type lkPagesType = 'home' | 'settings' | 'orders' | 'favorites' | 'delivery' | 'resetPass';

const LKHome = () => {
	const [lkPage, setLkPage] = React.useState<lkPagesType>('home');

	const dispatch = useAppDispatch();

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const firstName = useSelector((state: any) => state.userData.firstName);
	const lastName = useSelector((state: any) => state.userData.lastName);
	const email = useSelector((state: any) => state.userData.email);
	const phone = useSelector((state: any) => state.userData.phone);
	const birthday = useSelector((state: any) => state.userData.birthday);
	const city = useSelector((state: any) => state.userData.city);
	const subscribeNewsletter = useSelector((state: any) => state.userData.subscribeNewsletter);
	const token = useSelector((state: any) => state.userData.token);
	const page = useSelector((state: any) => state.orderList.page);

	const router = useRouter();

	const lkRedirectPage = (redirectTo: lkPagesType) => {
		router.push(
			{
				pathname: router.asPath.split('?')[0],
				query: {
					...router.query,
					name: [],
					section: redirectTo,
				},
			},
			undefined,
			{ shallow: true }
		);
	};

	const exitHandler = () => {
		dispatch(changeToken(false));
		dispatch(changeId(null));
		dispatch(changeFirstName(null));
		dispatch(changeLastName(null));
		dispatch(changeMiddleName(null));
		dispatch(changeEmail(null));
		dispatch(changePhone(null));
		dispatch(changeBirthday(null));
		dispatch(changeCity(null));
		deleteCookie('BITRIX_SM_SALE_UID');
		localStorage.removeItem('token');
		dispatch(getCheckoutProduct({ token: null }));
		dispatch(getFavoritesIds({ token: null }));
		router.push(router.asPath.split('?')[0] + routes.signIn);
	};

	React.useEffect(() => {
		dispatch(getOrderList({ pageSize: 5 }));
		if (localStorage.getItem('token')) {
			setLkPage(router.query.section as lkPagesType);
		}
	}, [token]);

	React.useEffect(() => {
		return () => {
			dispatch(changeOrderList([]));
			dispatch(changePage(1));
			dispatch(changeTotalPages(1));
		};
	}, []);

	if (lkPage === 'settings') {
		return (
			<LKSettings
				name={firstName || ''}
				secondName={lastName || ''}
				birthday={birthday || ''}
				city={city || ''}
				subscribeNewsletter={subscribeNewsletter || false}
				email={email || ''}
				phone={phone || ''}
				closeSettings={() => lkRedirectPage('home')}
				openResetPass={() => lkRedirectPage('resetPass')}
			/>
		);
	} else if (lkPage === 'resetPass') {
		return <LKResetPass closeResetPass={() => lkRedirectPage('settings')} />;
	} else if (lkPage === 'favorites') {
		router.push(router.asPath.split('?')[0] + routes.favorites + '&from=lk');
	} else if (lkPage === 'orders') {
		return <LKOrders closeOrders={() => lkRedirectPage('home')} />;
	} else if (lkPage === 'delivery') {
		return <PaymentAndDelivery closePaymentAndDelivery={() => lkRedirectPage('home')} />;
	}

	return (
		<div className={styles.lk}>
			<div>
				<Title
					text={firstName ? `, ${firstName}` : '!'}
					size={isMobile ? 22 : 28}
					marginBottom={isMobile ? 40 : 60}
					withSuisse
				/>
				<div onClick={() => lkRedirectPage('settings')} className={styles.link}>
					<p className={SuisseIntl.className}> </p>
					<Image alt="->" src={arrowDark} width={20} height={20} />
				</div>
				<div onClick={() => lkRedirectPage('orders')} className={styles.link}>
					<p className={SuisseIntl.className}></p>
					<Image alt="->" src={arrowDark} width={20} height={20} />
				</div>
				<div onClick={() => lkRedirectPage('favorites')} className={styles.link}>
					<p className={SuisseIntl.className}></p>
					<Image alt="->" src={arrowDark} width={20} height={20} />
				</div>
				<div onClick={() => lkRedirectPage('delivery')} className={styles.link}>
					<p className={SuisseIntl.className}>  </p>
				</div>
			</div>
			<div>
				<p> ?</p>
				<p>
					  &nbsp;
					<a href="https://api.whatsapp.com/send?phone=79151275549" target="_blank" rel="noreferrer">
						WhatsApp
					</a>{' '}
					  &nbsp; &nbsp;&nbsp;
					<a href="tel:+79151275549">+7&nbsp;915 127-55-49</a>
				</p>
				<Button className={styles.button} text="" isInvisible onClick={exitHandler} />
			</div>
		</div>
	);
};

export default React.memo(LKHome);
