import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { ChangePasswordApi, ChangePasswordModel, normolizeChangePassword } from '../models/ChangePasswordModel';

import { ApiResp, ChangePasswordApiData, IChangePasswordApi } from '../ChangePasswordApi/types';

export default class ChangeCurrentPasswordApi implements IChangePasswordApi {
	private readonly _apiStore = RootStore.apiStore;

	async ChangePassword(data: ChangePasswordApiData): Promise<ApiResp<ChangePasswordModel>> {
		const response = await this._apiStore.request<ApiResponse<ChangePasswordApi, ChangePasswordApi>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/me/change-password`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			response.data = normolizeChangePassword(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
