import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { IOrderPayApi, ApiResp, OrderPayRes } from './types';

export default class OrderPayStore implements IOrderPayApi {
	private readonly _apiStore = RootStore.apiStore;

	async orderPay(id: number): Promise<ApiResp<OrderPayRes>> {
		const response = await this._apiStore.request<ApiResponse<OrderPayRes, OrderPayRes>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/order/${id}/pay`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data: null,
		});
		return {
			success: response.success,
			data: response.data,
			status: response.status,
		};
	}
}
