import Image, { StaticImageData } from 'next/image';
import React from 'react';
import ProductItem from 'components/ProductItem';
import star from 'public/static/img/star.svg';
import darkStar from 'public/static/img/darkStar.svg';

import styles from './LKReview.module.scss';
import TextArea from 'components/UI/TextArea';
import Button from 'components/UI/Button';

import { useSelector } from 'react-redux';
import AddReviewApi from 'api/product/AddReviewApi';

const addReviewApi = new AddReviewApi();

type LKReviewTypes = {
	orderId: number;
	id: number;
	img: StaticImageData | string;
	name: string;
	price: number;
	color: string;
	size: string;
};

const LKReview = ({ orderId, id, img, name, price, color, size }: LKReviewTypes) => {
	const [reviewPoint, setReviewPoint] = React.useState<number>(5);
	const [reviewText, setReviewText] = React.useState<string>('');
	const [reviewIsSend, setReviewIsSend] = React.useState<boolean>(false);
	const [isCommentError, setIsCommentError] = React.useState<boolean>(false);
	const [errorText, setErrorText] = React.useState<string>('');

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const editReviewPoint = (point: number) => {
		setReviewPoint(point);
	};

	const editReviewText = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
		setReviewText(event.target.value);
		setIsCommentError(false);
	};

	const sendReview = () => {
		addReviewApi.addReview({ id: orderId }, { productId: id, comment: reviewText, stars: reviewPoint }).then(res => {
			if (res.data.errors) {
				if (res.data.errors.comment) {
					setIsCommentError(true);
					setErrorText(res.data.errors.comment[0]);
				}
			} else if (res.data.message === 'ok') {
				setReviewIsSend(true);
			}
		});
	};

	return (
		<>
			{!reviewIsSend ? (
				<>
					<ProductItem className={styles.product} isFullInfo data={{ id, img, name, price, color, size }} isReviwe />
					<div className={styles.review}>
						<div className={styles.stars}>
							<p> </p>
							<div>
								<Image
									alt="star"
									src={reviewPoint > 0 ? darkStar : star}
									width={24}
									height={24}
									onClick={() => editReviewPoint(1)}
								/>
								<Image
									alt="star"
									src={reviewPoint > 1 ? darkStar : star}
									width={24}
									height={24}
									onClick={() => editReviewPoint(2)}
								/>
								<Image
									alt="star"
									src={reviewPoint > 2 ? darkStar : star}
									width={24}
									height={24}
									onClick={() => editReviewPoint(3)}
								/>
								<Image
									alt="star"
									src={reviewPoint > 3 ? darkStar : star}
									width={24}
									height={24}
									onClick={() => editReviewPoint(4)}
								/>
								<Image
									alt="star"
									src={reviewPoint > 4 ? darkStar : star}
									width={24}
									height={24}
									onClick={() => editReviewPoint(5)}
								/>
							</div>
						</div>
						<TextArea
							className={styles.textArea}
							height={isMobile ? 120 : 155}
							placeholder="  "
							value={reviewText}
							onChange={editReviewText}
							isError={isCommentError}
							errorText={errorText}
						/>
						<Button text="" isInvisible className={styles.button} onClick={sendReview} />
					</div>
				</>
			) : (
				<>
					<p>  !</p>
				</>
			)}
		</>
	);
};

export default React.memo(LKReview);
