import Title from 'components/UI/Title';

import { formattedPrice } from 'helpers/utils';
import Image, { StaticImageData } from 'next/image';
import React from 'react';
import ProductItem from 'components/ProductItem';
import arrowDark from 'public/static/img/arrowDark.svg';

import styles from './OrderDetails.module.scss';
import LKReview from '../LKReview';
import { useSelector } from 'react-redux';
import { SuisseIntl } from 'helpers/utils/fonts';
import LKOrderBack from '../LKOrderBack';

import Button from 'components/UI/Button';
import OrderPayStore from 'api/checkout/OrderPayApi/OrderPayApi';
import { useRouter } from 'next/router';

type OrderDetailsTypes = {
	id: number;
	date: string;
	address: string;
	phone: string;
	status: {
		id: string;
		name: string;
	};
	payTemplate: string | null;
	openDetailesWithPay: number | null;
	price: number;
	priceWithoutDelivery: number;
	deliveryPrice: number;
	product: {
		id: number;
		img: StaticImageData | string;
		name: string;
		price: number;
		color: string;
		size: string;
		isReturned: boolean;
		statusText: string;
		quantity: number;
	}[];
	onCloseDetails: () => void;
};

const orderPayStore = new OrderPayStore();

const OrderDetails = ({
	id,
	date,
	address,
	phone,
	status,
	openDetailesWithPay,
	payTemplate,
	price,
	priceWithoutDelivery,
	deliveryPrice,
	product,
	onCloseDetails,
}: OrderDetailsTypes) => {
	const [reviewProduct, setReviewProduct] = React.useState<(typeof product)[0] | null>(null);
	const [orderBackIsOpen, setOrderBackIsOpen] = React.useState<boolean>(false);
	const [isFirstOpen, setIsFirstOpen] = React.useState<boolean>(true);

	const backRef = React.useRef<HTMLButtonElement | null>(null);
	const divRef = React.useRef(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const addReview = (id: number) => {
		setReviewProduct(id === null ? null : product.find(item => item.id === id));
	};

	React.useEffect(() => {
		const slotHtml = document
			.createRange()
			.createContextualFragment(payTemplate && payTemplate.replace('$("#payButton").on("click", payHandler);', '')); // Create a 'tiny' document and parse the html string
		divRef.current && (divRef.current.innerHTML = ''); // Clear the container
		divRef.current && divRef.current.appendChild(slotHtml); // Append the new content
		!isFirstOpen &&
			divRef.current &&
			divRef.current.querySelector('script').addEventListener('load', () => {
				setTimeout(() => {
					try {
						//@ts-ignore
						document.querySelector<HTMLButtonElement>('#payButton').onclick = payHandler;
						openDetailesWithPay === id && divRef.current.querySelector('#payButton').click();
					} catch (e) {
						document.querySelector<HTMLButtonElement>('#payButton').style.display = 'none';
					}
				}, 500);
			});
		setIsFirstOpen(false);
	}, [payTemplate, divRef.current]);

	return (
		<>
			{!orderBackIsOpen ? (
				<>
					<Title
						text={reviewProduct ? '' : ` #${id}`}
						size={isMobile ? 22 : 28}
						marginBottom={isMobile ? 32 : 40}
						withSuisse
					/>
					<>
						{reviewProduct === null ? (
							<>
								<div className={styles.orderInfo}>
									<p>{date}</p>
									<p>{address}</p>
									<p>{phone}</p>
									<Title text={status.name} withDot size={12} />
								</div>
								<div className={styles.products}>
									{product.map(item => {
										return (
											<ProductItem
												key={item.name}
												isFullInfo
												data={item}
												isReturned={item.isReturned}
												statusText={item.statusText}
												orderSuccess={status.id === 'F'}
												isOrder
												addReview={() => addReview(item.id)}
											/>
										);
									})}
								</div>
								<div className={styles.price}>
									<div>
										<p>
											{product.reduce((sum, item) => {
												return sum + item.quantity;
											}, 0)}{' '}
											{product.length === 1 ? '' : product.length > 4 ? '' : ''}
										</p>
										<p>{formattedPrice(priceWithoutDelivery)}</p>
									</div>
									<div>
										<p></p>
										<p>{formattedPrice(deliveryPrice)}</p>
									</div>
									<div>
										<p></p>
										<p>{formattedPrice(price)}</p>
									</div>
								</div>
								{payTemplate && <div ref={divRef} className={styles.cloudpay_button}></div>}
								{status.id === 'F' &&
									date &&
									(Date.now() -
										new Date(`${date.split('.')[1]}.${date.split('.')[0]}.${date.split('.')[2]}`).getTime()) /
										1000 /
										60 /
										60 /
										24 <
										14 && (
										<div className={styles.link} onClick={() => setOrderBackIsOpen(true)}>
											<p className={SuisseIntl.className}> </p>
											<Image alt="->" src={arrowDark} width={20} height={20} />
										</div>
									)}
							</>
						) : (
							<LKReview
								orderId={id}
								id={reviewProduct.id}
								img={reviewProduct.img}
								name={reviewProduct.name}
								price={reviewProduct.price}
								color={reviewProduct.color}
								size={reviewProduct.size}
							/>
						)}
					</>
					<button
						ref={backRef}
						id={reviewProduct !== null ? 'review' : ''}
						onClick={reviewProduct ? () => addReview(null) : onCloseDetails}
						className={`${styles.back} ${SuisseIntl.className}`}
					>
						
					</button>
				</>
			) : (
				<>
					<LKOrderBack backRef={backRef} product={product} id={id} />
					<button
						ref={backRef}
						onClick={() => setOrderBackIsOpen(false)}
						className={`${styles.back} ${SuisseIntl.className}`}
					>
						
					</button>
				</>
			)}
		</>
	);
};

export default React.memo(OrderDetails);
