import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { RegistrationDataApi, RegistrationModel, normolizeRegistration } from '../models/RegistrationModel';

import { ApiResp, RegistrationApiData, IRegistrationApi } from './types';

export default class RegistrationApi implements IRegistrationApi {
	private readonly _apiStore = RootStore.apiStore;

	async registration(data: RegistrationApiData): Promise<ApiResp<RegistrationModel>> {
		const response = await this._apiStore.request<ApiResponse<RegistrationDataApi, RegistrationDataApi>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/auth/sign-up`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			response.data = normolizeRegistration(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
