export type EditDataUserApi = {
	message: string;
	errors?: {
		firstName: string[];
		lastName: string[];
		birthday: string[];
		email: string[];
		city: string[];
		subscribeNewsletter: string[];
	};
	error: string;
};

export type EditDataUserModel = {
	message: string;
	errors?: {
		firstName: string[];
		lastName: string[];
		birthday: string[];
		email: string[];
		city: string[];
		subscribeNewsletter: string[];
	};
};

export const normolizeEditDataUser = (item: EditDataUserApi): EditDataUserModel => {
	return {
		message: item.message,
		errors: item.errors
			? {
					firstName: item.errors.firstName,
					lastName: item.errors.lastName,
					birthday: item.errors.birthday,
					email: item.errors.email,
					city: item.errors.city,
					subscribeNewsletter: item.errors.subscribeNewsletter,
			  }
			: null,
	};
};
