export type RegistrationDataApi = {
	token: string;
	type: string;
	expire: string;
	errors?: {
		firstName: string[];
		lastName: string[];
		password?: string[];
		passwordConfirmation?: string[];
		email: string[];
		phone: string[];
		agreement: string[];
		subscribeNewsletter: string[];
		codeConfirmation: string[];
	};
	error: string;
};

export type RegistrationModel = {
	token: string;
	errors?: {
		firstName: string[];
		lastName: string[];
		password?: string[];
		passwordConfirmation?: string[];
		email: string[];
		phone: string[];
		agreement: string[];
		subscribeNewsletter: string[];
		codeConfirmation: string[];
	};
};

export const normolizeRegistration = (item: RegistrationDataApi): RegistrationModel => {
	return {
		token: item.token,
		errors: item.errors
			? {
					firstName: item.errors.firstName,
					lastName: item.errors.lastName,
					password: item.errors.password,
					passwordConfirmation: item.errors.passwordConfirmation,
					email: item.errors.email,
					phone: item.errors.phone,
					agreement: item.errors.agreement,
					subscribeNewsletter: item.errors.subscribeNewsletter,
					codeConfirmation: item.errors.codeConfirmation,
			  }
			: null,
	};
};
