import Link from 'next/link';
import React from 'react';

import styles from './LKCheckbox.module.scss';

type LKCheckboxTypes = {
	value: boolean;
	id: string;
	label: string;
	isError?: boolean;
	link?: string;
	linkText?: string;
	onChange: () => void;
};

const LKCheckbox = ({ value, id, label, isError, link, linkText, onChange }: LKCheckboxTypes) => {
	return (
		<div className={styles.checkbox}>
			<input onChange={onChange} defaultChecked={value} type="checkbox" id={id} />
			<label
				className={isError === true ? styles.isError : ''}
				onSelect={event => event.preventDefault()}
				onMouseDown={event => event.preventDefault()}
				htmlFor={id}
			>
				{label}
				{link && (
					<Link href={link} target="_blank" rel="noreferrer">
						{linkText}
					</Link>
				)}
			</label>
		</div>
	);
};

export default React.memo(LKCheckbox);
