import React from 'react';

import styles from './TextArea.module.scss';

type TextAreaProps = {
	label?: string;
	value: string;
	placeholder?: string;
	height?: number;
	className?: string;
	isError?: boolean;
	errorText?: string;
	onChange: (event: React.ChangeEvent<HTMLTextAreaElement>) => void;
};

const TextArea = ({ label, value, placeholder, height, className, isError, errorText, onChange }: TextAreaProps) => {
	return (
		<div className={`${className} ${styles.textarea}`}>
			{label && <p>{label}</p>}
			<textarea
				style={{ height: height ? height : 'auto', border: isError ? '1px solid #EF3A3A' : '' }}
				placeholder={placeholder}
				value={value}
				onChange={onChange}
			/>
			{errorText && isError && <p className={styles.errorText}>{errorText}</p>}
		</div>
	);
};

export default React.memo(TextArea);
