import Image from 'next/image';
import React from 'react';
import arrowDark from 'public/static/img/arrowDark.svg';

import styles from './LKOrders.module.scss';

import Title from 'components/UI/Title';
import { formattedPrice } from 'helpers/utils';
import OrderDetails from '../OrderDetails';
import { useSelector } from 'react-redux';
import { SuisseIntl } from 'helpers/utils/fonts';
import SubTitle from 'components/UI/SubTitle';
import Button from 'components/UI/Button';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { getOrdersMore } from 'store/order/OrderListStore';
import Loader from 'components/UI/Loader/Loader';
import mock from 'public/static/img/mock.jpg';

import OrderPayStore from 'api/checkout/OrderPayApi/OrderPayApi';
import { useRouter } from 'next/router';

type LKOrdersTypes = {
	closeOrders: () => void;
};

const orderPayStore = new OrderPayStore();

const LKOrders = ({ closeOrders }: LKOrdersTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const orderList = useSelector((state: any) => state.orderList.orderList);
	const ordersIsLoad = useSelector((state: any) => state.orderList.ordersIsLoad);
	const page = useSelector((state: any) => state.orderList.page);
	const totalPages = useSelector((state: any) => state.orderList.totalPages);

	const [activeProduct, setActiveProduct] = React.useState<(typeof orderList)[0] | null>(null);
	const [openDetailesWithPay, setOpenDetailesWithPay] = React.useState<number | null>(null);

	const dispatch = useAppDispatch();

	const enterActiveProduct = (id: number | null, isPay: boolean) => {
		setOpenDetailesWithPay(isPay ? id : null);
		setActiveProduct(id === null ? null : orderList.find(item => item.id === id));
	};

	const loadMoreOrders = () => {
		if (page > totalPages) return;
		dispatch(getOrdersMore({ page, pageSize: 5 }));
	};

	const router = useRouter();

	React.useEffect(() => {
		if (activeProduct) {
			setActiveProduct(orderList.find(item => item.id === activeProduct.id));
		}
	}, [orderList]);

	return (
		<>
			{activeProduct === null ? (
				<>
					<Title text="" size={isMobile ? 22 : 28} marginBottom={isMobile ? 32 : 40} withSuisse />
					<div className={styles.products}>
						{!ordersIsLoad ? (
							<Loader imgWidth={50} />
						) : (
							<>
								{Array.isArray(orderList) && orderList.length === 0 && (
									<SubTitle text="   " size={16} marginTop={0} withSuisse />
								)}
								{orderList.map(item => {
									return (
										<>
											<div
												key={item.id}
												className={styles.favoritesItem}
												onClick={() => enterActiveProduct(item.id, false)}
											>
												<div className={styles.status}>
													<SubTitle text={item.status.name} withDot size={12} marginTop={0} />
												</div>
												<Image
													alt={item.status.name}
													src={item.product[0] ? item.product[0].img : mock}
													onLoadingComplete={img => (img.style.backgroundImage = 'none')}
													width={80}
													height={100}
													style={{
														maxWidth: '100%',
														height: 'auto',
													}}
												/>
												<div className={styles.infoBlock}>
													<div>
														<p># {item.id}</p>
														<p>{item.date}</p>
													</div>
													<p>{formattedPrice(item.price)}</p>
													<Image
														alt="arrow"
														src={arrowDark}
														width={isMobile ? 20 : 24}
														height={isMobile ? 20 : 24}
														style={{
															maxWidth: '100%',
															height: 'auto',
														}}
													/>
												</div>
											</div>
											{item.payTemplate && (
												<Button
													text=" "
													className={styles.payment}
													isInvisible
													onClick={() => enterActiveProduct(item.id, true)}
												/>
											)}
										</>
									);
								})}
								{ordersIsLoad && orderList[orderList.length - 1]?.payUrl && (
									<div className={styles.lineBottom}></div>
								)}
							</>
						)}
					</div>
					{page <= totalPages && ordersIsLoad && (
						<Button
							className={styles.loadMore}
							text=" "
							isInvisible
							isActive={isMobile}
							onClick={loadMoreOrders}
						/>
					)}
					<button onClick={closeOrders} className={`${styles.back} ${SuisseIntl.className}`}>
						
					</button>
				</>
			) : (
				<OrderDetails
					id={activeProduct.id}
					date={activeProduct.date}
					address={activeProduct.address}
					phone={activeProduct.phone}
					status={activeProduct.status}
					payTemplate={activeProduct.payTemplate}
					openDetailesWithPay={openDetailesWithPay}
					price={activeProduct.price}
					priceWithoutDelivery={activeProduct.priceWithoutDelivery}
					deliveryPrice={activeProduct.deliveryPrice}
					product={activeProduct.product}
					onCloseDetails={() => enterActiveProduct(null, false)}
				/>
			)}
		</>
	);
};

export default React.memo(LKOrders);
