import Button from 'components/UI/Button';
import Switcher from 'components/UI/Switcher';
import Title from 'components/UI/Title';

import React from 'react';
import { useSelector } from 'react-redux';
import Input from 'components/UI/Input';

import styles from './LKResetPass.module.scss';
import { SuisseIntl } from 'helpers/utils/fonts';
import ChangeCurrentPasswordApi from 'api/auth/ChangePasswordApi';

const changeCurrentPasswordApi = new ChangeCurrentPasswordApi();

type LKResetPassTypes = {
	closeResetPass: () => void;
};

const LKResetPass = ({ closeResetPass }: LKResetPassTypes) => {
	const [currentPass, setCurrentPass] = React.useState<string>('');
	const [newPass, setNewPass] = React.useState<string>('');
	const [retryNewPass, setRetryNewPass] = React.useState<string>('');
	const [isErrorCurrentPass, setIsErrorCurrentPass] = React.useState<boolean>(false);
	const [isErrorNewPass, setIsErrorNewPass] = React.useState<boolean>(false);
	const [isErrorRetryNewPass, setIsErrorRetryNewPass] = React.useState<boolean>(false);
	const [errorText, setErrorText] = React.useState<string>('');
	const [newPassIsSend, setNewPassIsSend] = React.useState<boolean>(false);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const onChangeInputs = (
		event: React.ChangeEvent<HTMLInputElement>,
		inputName: 'currentPass' | 'newPass' | 'retryNewPass'
	) => {
		if (inputName === 'currentPass') {
			setCurrentPass(event.target.value);
			setIsErrorCurrentPass(false);
		}
		if (inputName === 'newPass') {
			setNewPass(event.target.value);
			setIsErrorNewPass(false);
		}
		if (inputName === 'retryNewPass') {
			setRetryNewPass(event.target.value);
			setIsErrorRetryNewPass(false);
		}
	};

	const saveNewPass = () => {
		if (newPassIsSend) return;
		if (newPass === '') {
			setIsErrorNewPass(true);
			setErrorText(' ');
			return;
		}
		if (retryNewPass === '') {
			setIsErrorRetryNewPass(true);
			setErrorText(' ');
			return;
		}
		if (newPass !== retryNewPass) {
			setIsErrorCurrentPass(false);
			setIsErrorNewPass(true);
			setIsErrorRetryNewPass(true);
			setErrorText('  ');
			return;
		}
		setNewPassIsSend(true);
		changeCurrentPasswordApi
			.ChangePassword({ oldPassword: currentPass, newPassword: newPass, newPasswordConfirmation: retryNewPass })
			.then(res => {
				if (res.data.errors) {
					if (res.data.errors.oldPassword) {
						setIsErrorCurrentPass(true);
						setErrorText(res.data.errors.oldPassword[0]);
					}
					if (res.data.errors.newPassword) {
						setIsErrorNewPass(true);
						setErrorText(res.data.errors.newPassword[0]);
					}
					if (res.data.errors.newPasswordConfirmation) {
						setIsErrorRetryNewPass(true);
						setErrorText(res.data.errors.newPasswordConfirmation[0]);
					}
					setNewPassIsSend(false);
				} else if (res.data.message === 'ok') {
					closeResetPass();
				}
			});
	};

	return (
		<>
			<Title text=" " size={isMobile ? 22 : 28} marginBottom={isMobile ? 32 : 40} withSuisse />
			<Input
				className={`${styles.input} ${isErrorCurrentPass ? styles.isError : ''}`}
				placeholder=" "
				value={currentPass}
				type="password"
				isError={isErrorCurrentPass}
				errorText={errorText}
				onChange={event => onChangeInputs(event, 'currentPass')}
			/>
			<Input
				className={`${styles.input} ${isErrorNewPass ? styles.isError : ''}`}
				placeholder=" "
				value={newPass}
				type="password"
				isError={isErrorNewPass}
				errorText={errorText}
				onChange={event => onChangeInputs(event, 'newPass')}
			/>
			<Input
				className={`${styles.input} ${isErrorRetryNewPass ? styles.isError : ''}`}
				placeholder=" "
				value={retryNewPass}
				type="password"
				isError={isErrorRetryNewPass}
				errorText={errorText}
				onChange={event => onChangeInputs(event, 'retryNewPass')}
			/>
			<Button
				className={styles.button}
				text=" "
				isInvisible
				disabled={newPassIsSend}
				onClick={saveNewPass}
			/>
			<button onClick={closeResetPass} className={`${styles.back} ${SuisseIntl.className}`}>
				
			</button>
		</>
	);
};

export default React.memo(LKResetPass);
