import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { AddProductDataApi, AddProductModel, normolizeAddProduct } from '../Models/AddProductModel';

import { ApiResp, AddProductApiData, IAddProductApi } from '../AddProductApi/types';

export default class AddProductApi implements IAddProductApi {
	private readonly _apiStore = RootStore.apiStore;

	async addProduct(data: AddProductApiData): Promise<ApiResp<AddProductModel>> {
		const userUUID = localStorage.getItem('_user_uuid');
		const userIdObj = userUUID && JSON.parse(userUUID);

		const response = await this._apiStore.request<ApiResponse<AddProductDataApi, AddProductDataApi>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/basket/add?BITRIX_SM_SALE_UID=${userIdObj.BITRIX_SM_SALE_UID}&SALE_USER_ID=${userIdObj.SALE_USER_ID}`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			response.data = normolizeAddProduct(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
