import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import {
	ApiResp,
	RequestToChangePhoneApiData,
	IRequestToChangePhoneApi,
	RequestToChangePhoneResponseData,
} from '../RequestToChangePhoneApi/types';

export default class RequestToChangePhoneApi implements IRequestToChangePhoneApi {
	private readonly _apiStore = RootStore.apiStore;

	async getRequestToChangePhone(data: RequestToChangePhoneApiData): Promise<ApiResp> {
		const response = await this._apiStore.request<
			ApiResponse<RequestToChangePhoneResponseData, RequestToChangePhoneResponseData>
		>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/me/change-phone/request`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
