import React from 'react';

import styles from './PersonalArea.module.scss';
import closeDark from 'public/static/img/closeDark.svg';
import Image from 'next/image';
import { useRouter } from 'next/router';
import LKContent from './views/LKContent';
import routes from 'helpers/routes';
import { useSelector } from 'react-redux';

type PersonalAreaTypes = {
	isOpen: boolean;
	action: string | string[];
	onRequestClose: () => void;
};

const PersonalArea = ({ isOpen, action, onRequestClose }: PersonalAreaTypes) => {
	const lkRef = React.useRef<HTMLDivElement | null>(null);

	const router = useRouter();

	React.useEffect(() => {
		const token = localStorage.getItem('token');
		if (action === undefined || action === 'favorites') return;
		if ((action === 'sign-in' || action === 'registration') && token) {
			router.push(router.asPath.split('?')[0] + routes.lk, undefined, { shallow: true });
		} else if (action !== 'sign-in' && action !== 'registration' && !token) {
			router.push(router.asPath.split('?')[0] + routes.signIn, undefined, { shallow: true });
			sessionStorage.setItem('authRedirect', 'true');
		}
	}, [action]);

	const onCloseLK = (event: React.MouseEvent<HTMLDivElement>) => {
		const { target } = event;

		if (!lkRef.current?.contains(target as Node)) {
			onRequestClose();
		}
	};

	return (
		<>
			{isOpen ? (
				<div onMouseDown={onCloseLK} className={styles.lkBGC}>
					<div ref={lkRef} className={styles.lk}>
						<Image
							alt="X"
							onClick={onRequestClose}
							src={closeDark}
							width={24}
							height={24}
							style={{
								maxWidth: '100%',
								height: 'auto',
							}}
						/>
						<div className={styles.content}>
							<LKContent action={action} />
						</div>
					</div>
				</div>
			) : (
				<></>
			)}
		</>
	);
};

export default React.memo(PersonalArea);
