import Title from 'components/UI/Title';

import React from 'react';
import Input from 'components/UI/Input';
import MaskedInput from 'react-text-mask';

import styles from './Registration.module.scss';
import LKCheckbox from '../LKCheckbox';
import Button from 'components/UI/Button';
import ActiveLink from 'components/ActiveLink';
import { useRouter } from 'next/router';
import routes from 'helpers/routes';
import { useSelector } from 'react-redux';
import { SuisseIntl } from 'helpers/utils/fonts';
import RegistrationApi from 'api/auth/RegistrationApi';
import { getUserData } from 'store/user/UserDataStore';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { getCheckoutProduct } from 'store/product/CheckoutProductStore';
import { getFavoritesIds } from 'store/favorites/FavoritesStore';
import SmsConfirationApi from 'api/auth/SmsConfirationApi';
import ym from 'react-yandex-metrika';

const registrationApi = new RegistrationApi();
const smsConfirationApi = new SmsConfirationApi();

const Registration = () => {
	const [name, setName] = React.useState<string>('');
	const [isErrorName, setIsErrorName] = React.useState<boolean>(false);
	const [secondName, setSecondName] = React.useState<string>('');
	const [isErrorSecondName, setIsErrorSecondName] = React.useState<boolean>(false);
	const [email, setEmail] = React.useState<string>('');
	const [isErrorEmail, setIsErrorEmail] = React.useState<boolean>(false);
	const [password, setPassword] = React.useState<string>('');
	const [isErrorPassword, setIsErrorPassword] = React.useState<boolean>(false);
	const [passwordRetry, setPasswordRetry] = React.useState<string>('');
	const [isErrorPasswordRetry, setIsErrorPasswordRetry] = React.useState<boolean>(false);
	const [phone, setPhone] = React.useState<string>('');
	const [isErrorPhone, setIsErrorPhone] = React.useState<boolean>(false);
	const [isSubscribe, setIsSubscribe] = React.useState<boolean>(false);
	const [isAgree, setIsAgree] = React.useState<boolean>(false);
	const [isErrorAgree, setIsErrorAgree] = React.useState<boolean>(false);
	const [errorText, setErrorText] = React.useState<string>('');
	const [smsTimer, setSmsTimer] = React.useState<number | null>(null);
	const [smsCode, setSmsCode] = React.useState<string>('');
	const [isSmsCodeError, setIsSmsCodeError] = React.useState<boolean>(false);
	const [registrationDataIsSend, setRegistrationDataIsSend] = React.useState<boolean>(false);
	const [smsIsSended, setSmsIsSended] = React.useState<boolean>(true);

	const interval = React.useRef(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const router = useRouter();

	const dispatch = useAppDispatch();

	const onChangeName = (event: React.ChangeEvent<HTMLInputElement>) => {
		setIsErrorName(false);
		setName(event.target.value);
	};
	const onChangeSecondName = (event: React.ChangeEvent<HTMLInputElement>) => {
		setIsErrorSecondName(false);
		setSecondName(event.target.value);
	};

	const onChangeEmail = (event: React.ChangeEvent<HTMLInputElement>) => {
		setIsErrorEmail(false);
		setEmail(event.target.value);
	};

	const onChangePassword = (event: React.ChangeEvent<HTMLInputElement>) => {
		setIsErrorPassword(false);
		setIsErrorPasswordRetry(false);
		setPassword(event.target.value);
	};

	const onChangePasswordRetry = (event: React.ChangeEvent<HTMLInputElement>) => {
		setIsErrorPasswordRetry(false);
		setIsErrorPassword(false);
		setPasswordRetry(event.target.value);
	};

	const onChangePhoneHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
		setIsErrorPhone(false);
		setPhone(
			event.target.value[0] === '+'
				? event.target.value[0] + event.target.value.slice(1, event.target.value.length).replace(/[^\d]/g, '')
				: event.target.value.replace(/[^\d]/g, '')
		);
	};

	const onSubscribeHandler = () => {
		setIsSubscribe(!isSubscribe);
	};

	const onAgreeHandler = () => {
		setIsErrorAgree(false);
		setIsAgree(!isAgree);
	};

	const onRegistrationSubmit = () => {
		if (registrationDataIsSend) return;
		if (password !== passwordRetry) {
			setIsErrorPassword(true);
			setIsErrorPasswordRetry(true);
			setErrorText('  ');
			setIsErrorName(false);
			setIsErrorSecondName(false);
			setIsErrorEmail(false);
			setIsErrorPhone(false);
			setIsErrorAgree(false);
			return;
		} else if (password.length < 8) {
			setIsErrorPassword(true);
			setErrorText('    8 ');
			setIsErrorPasswordRetry(false);
			setIsErrorName(false);
			setIsErrorSecondName(false);
			setIsErrorEmail(false);
			setIsErrorPhone(false);
			setIsErrorAgree(false);
			return;
		} else if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)) {
			setIsErrorEmail(true);
			setErrorText('  email');
			setIsErrorPassword(false);
			setIsErrorPasswordRetry(false);
			setIsErrorName(false);
			setIsErrorSecondName(false);
			setIsErrorPhone(false);
			setIsErrorAgree(false);
			return;
		} else {
			setIsErrorPhone(false);
			setIsSmsCodeError(false);
			if (name === '') {
				setIsErrorName(true);
				setErrorText(' ');
				return;
			}
			if (secondName === '') {
				setIsErrorSecondName(true);
				setErrorText(' ');
				return;
			}
			if (email === '') {
				setIsErrorEmail(true);
				setErrorText(' ');
				return;
			}
			if (password === '') {
				setIsErrorPassword(true);
				setErrorText(' ');
				return;
			}
			if (passwordRetry === '') {
				setIsErrorPasswordRetry(true);
				setErrorText(' ');
				return;
			}
			if (phone === '') {
				setIsErrorPhone(true);
				setErrorText(' ');
				return;
			}
			if (!isAgree) {
				setIsErrorAgree(true);
				return;
			}
			setRegistrationDataIsSend(true);
			registrationApi
				.registration({
					firstName: name,
					lastName: secondName,
					password: password,
					passwordConfirmation: passwordRetry,
					email: email,
					phone: phone,
					agreement: isAgree,
					subscribeNewsletter: isSubscribe,
					codeConfirmation: smsCode,
				})
				.then(res => {
					setRegistrationDataIsSend(false);
					if (res.data.errors && res.data.errors.firstName) {
						setIsErrorName(true);
						setErrorText(res.data.errors.firstName[0]);
						return;
					}
					if (res.data.errors && res.data.errors.lastName) {
						setIsErrorSecondName(true);
						setErrorText(res.data.errors.lastName[0]);
						return;
					}
					if (res.data.errors && res.data.errors.password) {
						setIsErrorPassword(true);
						setErrorText(res.data.errors.password[0]);
						return;
					}
					if (res.data.errors && res.data.errors.passwordConfirmation) {
						setIsErrorPasswordRetry(true);
						setErrorText(res.data.errors.passwordConfirmation[0]);
						return;
					}
					if (res.data.errors && res.data.errors.email) {
						setIsErrorEmail(true);
						setErrorText(res.data.errors.email[0]);
						return;
					}
					if (res.data.errors && res.data.errors.phone) {
						setIsErrorPhone(true);
						setErrorText(res.data.errors.phone[0]);
						return;
					}
					if (res.data.errors && res.data.errors.agreement) {
						setIsErrorAgree(true);
						return;
					}
					if (res.data.errors && res.data.errors.codeConfirmation) {
						if (!smsCode) {
							setIsErrorPhone(true);
							setErrorText(' sms     ');
						} else {
							setIsSmsCodeError(true);
							setErrorText(res.data.errors.codeConfirmation[0]);
						}
						return;
					}
					if (res.data.token) {
						process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID && ym('reachGoal', 'registration');
						localStorage.setItem('token', res.data.token);
						dispatch(getUserData({ token: res.data.token })).then(() => {
							router.push(router.asPath.split('?')[0] + routes.lk);
							dispatch(getCheckoutProduct({ token: localStorage.getItem('token') }));
							dispatch(getFavoritesIds({ token: localStorage.getItem('token') }));
						});
					}
				});
		}
	};

	const startSmsTimer = () => {
		if (!smsIsSended) return;
		setSmsIsSended(false);
		setIsErrorPassword(false);
		setIsErrorPasswordRetry(false);
		setIsErrorName(false);
		setIsErrorSecondName(false);
		setIsErrorEmail(false);
		setIsErrorPhone(false);
		setIsErrorAgree(false);
		smsConfirationApi.getSmsConfiration({ phone }, true).then(res => {
			setSmsIsSended(true);
			if (res.data.message === 'ok') {
				setSmsTimer(60);
				interval.current = setInterval(() => {
					setSmsTimer(prev => prev - 1);
				}, 1000);
			} else if (res.data.errors && res.data.errors.phone) {
				setIsErrorPhone(true);
				setErrorText(res.data.errors.phone[0]);
			}
		});
	};

	const onChangeSmsCode = (event: React.ChangeEvent<HTMLInputElement>) => {
		setIsSmsCodeError(false);
		setIsErrorPhone(false);
		setSmsCode(event.target.value);
	};

	React.useEffect(() => {
		if (smsTimer === 0) clearInterval(interval.current);
	}, [smsTimer, interval]);

	return (
		<>
			<Title text=" " size={isMobile ? 22 : 28} marginBottom={isMobile ? 32 : 40} withSuisse />
			<Input
				className={`${styles.input} ${isErrorName ? styles.isError : ''}`}
				value={name}
				placeholder=""
				type="text"
				isError={isErrorName}
				errorText={errorText}
				onChange={onChangeName}
			/>
			<Input
				className={`${styles.input} ${isErrorSecondName ? styles.isError : ''}`}
				value={secondName}
				placeholder=""
				type="text"
				isError={isErrorSecondName}
				errorText={errorText}
				onChange={onChangeSecondName}
			/>
			<Input
				className={`${styles.input} ${isErrorEmail ? styles.isError : ''}`}
				value={email}
				placeholder="Email"
				type="text"
				isError={isErrorEmail}
				errorText={errorText}
				onChange={onChangeEmail}
			/>
			<Input
				className={`${styles.input} ${isErrorPassword ? styles.isErrorPass : ''}`}
				value={password}
				placeholder=""
				type="password"
				label="  8 "
				isError={isErrorPassword}
				errorText={errorText}
				onChange={onChangePassword}
			/>
			<Input
				className={`${styles.input} ${isErrorPasswordRetry ? styles.isError : ''}`}
				value={passwordRetry}
				placeholder=" "
				type="password"
				isError={isErrorPasswordRetry}
				errorText={errorText}
				onChange={onChangePasswordRetry}
			/>
			<div className={styles.phoneBlock}>
				{localStorage.getItem('location') === 'RU' ? (
					<MaskedInput
						className={`${styles.inputMask} ${isErrorPhone ? styles.errorPhone : ''}`}
						mask={['+', '7', ' ', /[1-6]|[8-9]/, /\d/, /\d/, ' ', /\d/, /\d/, /\d/, ' ', /\d/, /\d/, ' ', /\d/, /\d/]}
						guide
						id="inputMask"
						placeholder="+7 999 000 00 00"
						type="tel"
						onChange={onChangePhoneHandler}
						value={phone}
					/>
				) : (
					<Input
						className={`${styles.input} ${isErrorPhone ? styles.errorPhone : ''}`}
						value={phone}
						placeholder="+7 999 000 00 00"
						type="tel"
						onChange={onChangePhoneHandler}
					/>
				)}
				{localStorage.getItem('location') === 'RU' && (
					<>
						{phone.length >= 11 && smsTimer === null && <p onClick={startSmsTimer}> </p>}
						{phone.length >= 11 && smsTimer !== null && smsTimer !== 0 && <p>{`00:${smsTimer}`}</p>}
						{phone.length >= 11 && smsTimer !== null && smsTimer === 0 && (
							<p onClick={startSmsTimer}>  </p>
						)}
					</>
				)}
			</div>
			{isErrorPhone && errorText && <p className={styles.errorText}>{errorText}</p>}
			{smsTimer !== null && localStorage.getItem('location') === 'RU' && (
				<Input
					className={styles.smsInput}
					value={smsCode}
					placeholder=""
					type="number"
					errorText={errorText}
					isError={isSmsCodeError}
					onChange={onChangeSmsCode}
				/>
			)}
			<div className={styles.checkboxBlock}>
				<LKCheckbox value={isSubscribe} id="isSubscribe" label="  " onChange={onSubscribeHandler} />
				<LKCheckbox
					value={isAgree}
					id="isAgree"
					label=" ()  "
					isError={isErrorAgree}
					link={routes.agreement}
					linkText=""
					onChange={onAgreeHandler}
				/>
			</div>
			<Button
				className={styles.reg}
				isActive
				isInvisible
				text=""
				disabled={registrationDataIsSend}
				onClick={onRegistrationSubmit}
			/>
			<ActiveLink href={router.asPath.split('?')[0] + routes.signIn}>
				<button className={`${styles.back} ${SuisseIntl.className}`}></button>
			</ActiveLink>
		</>
	);
};

export default React.memo(Registration);
