import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, ChangePhoneApiData, IChangePhoneApi, ChangePhoneResponseData } from '../ChangePhoneApi/types';

export default class ChangePhoneApi implements IChangePhoneApi {
	private readonly _apiStore = RootStore.apiStore;

	async getChangePhone(data: ChangePhoneApiData): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<ChangePhoneResponseData, ChangePhoneResponseData>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/me/change-phone/change`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
