export type ChangePasswordApi = {
	message: string;
	errors?: {
		oldPassword: string[];
		newPassword: string[];
		newPasswordConfirmation: string[];
	};
	error: string;
};

export type ChangePasswordModel = {
	message: string;
	errors?: {
		oldPassword: string[];
		newPassword: string[];
		newPasswordConfirmation: string[];
	};
};

export const normolizeChangePassword = (item: ChangePasswordApi): ChangePasswordModel => {
	return {
		message: item.message,
		errors: item.errors
			? {
					oldPassword: item.errors.oldPassword,
					newPassword: item.errors.newPassword,
					newPasswordConfirmation: item.errors.newPasswordConfirmation,
			  }
			: null,
	};
};
