import ReturnOrderApi from 'api/order/ReturnOrderApi';
import ProductItem from 'components/ProductItem';
import Button from 'components/UI/Button';
import Dropdown from 'components/UI/Dropdown';
import RadioGroup from 'components/UI/RadioGroup';
import TextArea from 'components/UI/TextArea';
import Title from 'components/UI/Title';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import useDebounce from 'helpers/hooks/useDebounce';
import { SuisseIntl } from 'helpers/utils/fonts';
import { StaticImageData } from 'next/image';
import React, { RefObject } from 'react';
import { useSelector } from 'react-redux';
import {
	changeCities,
	changeCountryies,
	changeCurrentCity,
	changeCurrentCountry,
	getCityList,
	getCountryList,
} from 'store/checkout/LocationDataStore';
import { ordersReload } from 'store/order/OrderListStore';

import styles from './LKOrderBack.module.scss';

const returnOrderApi = new ReturnOrderApi();

type productType = {
	id: number;
	img: StaticImageData | string;
	name: string;
	price: number;
	color: string;
	size: string;
	isReturned: boolean;
};

type productsType = {
	id: number;
	img: StaticImageData | string;
	name: string;
	price: number;
	color: string;
	size: string;
	isSelect: boolean;
	isReturned: boolean;
};

type LKOrderBackTypes = {
	product: productType[];
	backRef: RefObject<HTMLButtonElement>;
	id: number;
};

type RadioListTypes = {
	id: number;
	value: boolean;
	label: string;
	isPrompt?: boolean;
}[];

const radioList = [
	{
		id: 7,
		value: true,
		label: '   ',
	},
	{
		id: 8,
		value: false,
		label: 'T   ',
	},
	{
		id: 9,
		value: false,
		label: '  ',
	},
	{
		id: 10,
		value: false,
		label: '',
	},
];

const radioDeliveryList = [
	{
		id: 11,
		value: true,
		label: '',
	},
	{
		id: 12,
		value: false,
		label: ' ',
	},
];

const LKOrderBack = ({ product, backRef, id }: LKOrderBackTypes) => {
	const [products, setProducts] = React.useState<productsType[]>(
		product.filter(item => !item.isReturned).map(item => ({ ...item, isSelect: false }))
	);
	const [productBack, setProductBack] = React.useState<productType[]>([]);
	const [isOpenProductBack, setIsOpenProductBack] = React.useState<boolean>(false);
	const [radioBackList, setRadioBackList] = React.useState<RadioListTypes>(radioList);
	const [radioBackDeliveryList, setRadioBackDeliveryList] = React.useState<RadioListTypes>(radioDeliveryList);
	const [comment, setComment] = React.useState<string>(radioList[0].label);
	const [isMyMessage, setIsMyMessage] = React.useState<boolean>(false);
	const [city, setCity] = React.useState<string>('');
	const [country, setCountry] = React.useState<string>('');
	const [productBackIsSend, setProductBackIsSend] = React.useState<boolean>(false);
	const [productBackInfoIsOpen, setProductBackInfoIsOpen] = React.useState<boolean>(false);
	const [countryIsReadonly, setCountryIsReadonly] = React.useState<boolean>(true);
	const [cityIsReadonly, setCityIsReadonly] = React.useState<boolean>(true);
	const [countCity, setCountCity] = React.useState<number>(0);
	const [offsetCity, setOffsetCity] = React.useState<number>(0);
	const [isErrorCity, setIsErrorCity] = React.useState<boolean>(false);
	const [isErrorComment, setIsErrorComment] = React.useState<boolean>(false);
	const [errorComment, setErrorComment] = React.useState<string>('');

	const countryies = useSelector((state: any) => state.locationData.countryies);
	const cities = useSelector((state: any) => state.locationData.cities);
	const page = useSelector((state: any) => state.orderList.page);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const dispatch = useAppDispatch();

	const debounceCountryies = useDebounce(country, 300);
	const debounceCities = useDebounce(city, 300);

	const selectProductBack = (id: number) => {
		setProductBack(prev => {
			const isAdd = !prev.find(item => item.id === id);
			if (isAdd) {
				return [...prev, product.find(item => item.id === id)];
			} else {
				return prev.filter(item => item.id !== id);
			}
		});
		setProducts(prev => {
			const isAdd = prev.some(item => item.id === id && item.isSelect === false);
			if (isAdd) {
				return prev.map(item => {
					if (item.id === id) {
						return { ...item, isSelect: true };
					}
					return item;
				});
			} else {
				return prev.map(item => {
					if (item.id === id) {
						return { ...item, isSelect: false };
					}
					return item;
				});
			}
		});
	};

	const IsOpenProductBackHandler = () => {
		if (productBack.length === 0) return;
		setIsOpenProductBack(true);
	};

	const selectRadioType = (id: number) => {
		setRadioBackList(prev => {
			return prev.map(item => {
				if (item.id === id) {
					return { ...item, value: true };
				} else {
					return { ...item, value: false };
				}
			});
		});
		if (id === 10) {
			setComment('');
			setIsMyMessage(true);
			return;
		}
		setIsMyMessage(false);
		setComment(radioBackList.find(item => item.id === id).label);
	};

	const selectRadioDeliveryType = (id: number) => {
		setRadioBackDeliveryList(prev => {
			return prev.map(item => {
				if (item.id === id) {
					return { ...item, value: true };
				} else {
					return { ...item, value: false };
				}
			});
		});
	};

	const changeComment = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
		setComment(event.target.value);
		setIsErrorComment(false);
		setErrorComment('');
	};

	const changeCountry = (event: React.ChangeEvent<HTMLInputElement>) => {
		setCountry(event.target.value);
		setCountryIsReadonly(false);
		setCity('');
		setCityIsReadonly(false);
		setOffsetCity(0);
		setCountCity(0);
		dispatch(changeCurrentCity({ currentCity: '', currentCityCode: '' }));
	};

	const clickOnDropDataCountry = (event: React.MouseEvent<HTMLDivElement>) => {
		setCountry(countryies.find(item => item.name === event.currentTarget.textContent).name);
		dispatch(changeCurrentCountry(countryies.find(item => item.name === event.currentTarget.textContent).name));
		dispatch(changeCountryies([]));
		setCountryIsReadonly(true);
	};

	const changeCity = (event: React.ChangeEvent<HTMLInputElement>) => {
		setCity(event.target.value);
		setCityIsReadonly(false);
		setOffsetCity(0);
		setCountCity(0);
		setIsErrorCity(false);
	};

	const clickOnDropDataCity = (event: React.MouseEvent<HTMLDivElement>) => {
		const selectedCity = cities.find(item => item.name === event.currentTarget.textContent);
		setCity(selectedCity.name);
		dispatch(
			changeCurrentCity({
				currentCity: selectedCity.name,
				currentCityCode: selectedCity.code,
			})
		);
		dispatch(changeCities([]));
		setCityIsReadonly(true);
	};

	const loadMoreCity = () => {
		dispatch(
			getCityList({
				countryId: Array.isArray(countryies) ? countryies.find(item => item.name === debounceCountryies)?.id : '',
				query: debounceCities,
				limit: 6,
				offset: offsetCity,
			})
		).then(res => {
			const response = res.payload as any;
			setCountCity(response.data.meta.count);
			setOffsetCity(offsetCity + response.data.meta.offset);
		});
	};

	const productBackSendHandler = () => {
		returnOrderApi
			.returnOrder(
				{ id },
				{
					productIds: productBack.map(item => item.id),
					message: comment,
					city: city,
					deliveryMethod: radioBackDeliveryList.find(item => item.value === true).label,
				}
			)
			.then(res => {
				if (res.data.errors && res.data.errors.city) {
					setIsErrorCity(true);
					return;
				}
				if (res.data.errors && res.data.errors.message) {
					setIsErrorComment(true);
					setErrorComment(res.data.errors.message[0]);
					return;
				}
				if (res.data.message === 'ok') {
					setProductBackIsSend(true);
					dispatch(ordersReload({ page: 1, pageSize: 5 * (page - 1) }));
				}
			});
	};

	const openInfoHandler = (event: React.MouseEvent<HTMLAnchorElement>) => {
		setProductBackInfoIsOpen(true);
		setProductBackIsSend(false);
		event.preventDefault();
	};

	React.useEffect(() => {
		dispatch(getCountryList({ query: debounceCountryies }));
	}, [debounceCountryies]);

	React.useEffect(() => {
		dispatch(
			getCityList({
				countryId: Array.isArray(countryies) ? countryies.find(item => item.name === debounceCountryies)?.id : '',
				query: debounceCities,
				limit: 6,
				offset: offsetCity,
			})
		).then(res => {
			const response = res.payload as any;
			setCountCity(response.data.meta.count);
			setOffsetCity(offsetCity + response.data.meta.offset);
		});
	}, [debounceCities]);

	React.useEffect(() => {
		if (localStorage.getItem('location') === 'RU') {
			setCountry('');
		}
	}, []);

	if (productBackIsSend) {
		return (
			<>
				<Title text=" " size={isMobile ? 22 : 28} marginBottom={isMobile ? 24 : 40} withSuisse />
				<p className={styles.successSendText}>
					  &nbsp;,    &nbsp; . &nbsp; 
					 {' '}
					<a href="#" onClick={openInfoHandler}>
						&nbsp;
					</a>
					.
				</p>
			</>
		);
	} else if (productBackInfoIsOpen) {
		return (
			<>
				<Title text=" " size={isMobile ? 22 : 28} marginBottom={isMobile ? 24 : 40} withSuisse />
				<p className={`${styles.successSendText} ${styles.withUnderline}`}>
					      ,  &nbsp;&nbsp; &nbsp;
					&nbsp;    , &nbsp; 14&nbsp; &nbsp; . 
					       &nbsp;  ,
					&nbsp;   &nbsp;     &nbsp; .
				</p>
				<p className={`${styles.successSendTitle} ${SuisseIntl.className}`}>  </p>
				<p className={styles.successSendText}>
					     &nbsp;, &nbsp;    &nbsp;
					10&nbsp;  &nbsp;   &nbsp; , &nbsp;&nbsp; ,
					&nbsp;   .
				</p>
				<p className={`${styles.successSendText} ${styles.withUnderline}`}>
					&nbsp;        ,   
					&nbsp;,  &nbsp; &nbsp; 10&nbsp;  &nbsp; 
					 &nbsp; &nbsp; .
				</p>
				<p className={`${styles.successSendTitle} ${SuisseIntl.className}`}> </p>
				<p className={`${styles.successSendTitle} ${SuisseIntl.className} ${styles.subTitle}`}>300 .</p>
				<p className={styles.successSendText}>
					&nbsp;,  , - &nbsp; 
				</p>
				<p className={`${styles.successSendTitle} ${SuisseIntl.className}  ${styles.subTitle}`}>490 .</p>
				<p className={styles.successSendText}> </p>
				<p className={`${styles.successSendTitle} ${SuisseIntl.className}  ${styles.subTitle}`}>690 .</p>
				<p className={styles.successSendText}>&nbsp; &nbsp;</p>
				<p className={`${styles.successSendTitle} ${SuisseIntl.className}  ${styles.subTitle}`}>2000 .</p>
				<p className={`${styles.successSendText} ${styles.withUnderline}`}>&nbsp; </p>
				<p className={`${styles.successSendTitle} ${SuisseIntl.className}`}> </p>
				<p className={`${styles.successSendTitle} ${SuisseIntl.className} ${styles.bigSubTitle}`}>
					  
				</p>
				<p className={styles.successSendText}>
					     &nbsp;   &nbsp; &nbsp; . 
					   &nbsp;  .
				</p>
				<p className={`${styles.successSendTitle} ${SuisseIntl.className} ${styles.bigSubTitle}`}>
					    
				</p>
				<p className={styles.successSendText}>
					   &nbsp;    &nbsp; . 
					&nbsp;       &nbsp;  . 
					 {' '}
					<a href="#" target="_blank">
						
					</a>
					.
				</p>
				<p className={`${styles.successSendTitle} ${SuisseIntl.className} ${styles.bigSubTitle}`}></p>
				<p className={styles.successSendText}>
					&nbsp;     &nbsp;  .    
					&nbsp; :      &nbsp; - 
					 .  &nbsp;      
					&nbsp;  .   {' '}
					<a href="#" target="_blank">
						
					</a>
					.
				</p>
			</>
		);
	}

	return (
		<>
			<Title
				text={isOpenProductBack ? ' ' : ' '}
				size={isMobile ? 22 : 28}
				marginBottom={isMobile ? 32 : 40}
				withSuisse
			/>
			{!isOpenProductBack ? (
				<>
					<div className={styles.products}>
						{products.map(item => {
							return (
								<ProductItem
									key={item.name}
									isFullInfo
									data={item}
									isReturned={item.isReturned}
									isBack
									selectProductBack={() => selectProductBack(item.id)}
								/>
							);
						})}
					</div>
					<Button
						disabled={productBack.length === 0}
						className={styles.button}
						text=""
						isInvisible
						onClick={IsOpenProductBackHandler}
					/>
				</>
			) : (
				<div className={styles.productsBack}>
					<div>
						{productBack.map(item => {
							return <ProductItem key={item.name} isFullInfo data={item} isReviwe />;
						})}
					</div>
					<p> </p>
					<RadioGroup radioList={radioBackList} withoutBorder selectType={id => selectRadioType(id)} />
					{isMyMessage && (
						<TextArea
							className={styles.textarea}
							value={comment}
							onChange={changeComment}
							placeholder=""
							height={120}
							isError={isErrorComment}
							errorText={errorComment}
						/>
					)}
					<div className={styles.location}>
						<p></p>
						<Dropdown
							placeholder=""
							value={country}
							readonly={countryIsReadonly}
							dropdata={countryies}
							withSuisse
							onChange={changeCountry}
							clickOnDropData={clickOnDropDataCountry}
						/>
						<p></p>
						<Dropdown
							placeholder=""
							value={city}
							readonly={cityIsReadonly}
							dropdata={cities}
							infinityScroll
							isError={isErrorCity}
							errorText=" "
							hasMore={Array.isArray(cities) ? countCity > cities.length : false}
							onChange={changeCity}
							clickOnDropData={clickOnDropDataCity}
							fetchData={loadMoreCity}
						/>
					</div>
					<p> </p>
					<RadioGroup radioList={radioBackDeliveryList} withoutBorder selectType={id => selectRadioDeliveryType(id)} />
					<Button
						className={`${styles.button} ${styles.sendBtn}`}
						text=""
						isInvisible
						onClick={productBackSendHandler}
					/>
				</div>
			)}
		</>
	);
};

export default React.memo(LKOrderBack);
