import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, ReturnOrderApiData, IReturnOrderApi, ReturnOrderResp, ReturnOrderApiParams } from './types';

export default class ReturnOrderApi implements IReturnOrderApi {
	private readonly _apiStore = RootStore.apiStore;

	async returnOrder(params: ReturnOrderApiParams, data: ReturnOrderApiData): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<ReturnOrderResp, ReturnOrderResp>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/order/${params.id}/return`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
