import Link from 'next/link';
import { useRouter } from 'next/router';
import React from 'react';

type ActiveLinkTypes = {
	children: React.ReactElement<any, any>;
	href: string;
	className?: string;
	isBlank?: boolean;
};

const ActiveLink = ({ children, href, className, isBlank }: ActiveLinkTypes) => {
	const [scrollCount, setScrollCount] = React.useState<number>(0);
	const [currentScrollCount, setCurrentScrollCount] = React.useState<number>(0);

	const router = useRouter();

	const handleMove = (event: React.MouseEvent) => {
		setCurrentScrollCount(event.currentTarget.parentElement.parentElement.scrollLeft);
	};

	const handleClick = (event: React.MouseEvent) => {
		event.preventDefault();
		setScrollCount(currentScrollCount);
		if (scrollCount - currentScrollCount < 20 && scrollCount - currentScrollCount > -20) {
			if (isBlank) {
				window.open(href, '_blank').focus();
			} else {
				router.push(href);
			}
		}
		setCurrentScrollCount(0);
	};

	const onClickHandler = (event: React.MouseEvent) => {
		event.preventDefault();
	};

	return (
		<Link
			className={className}
			href={href}
			style={{ textDecoration: 'none', cursor: 'pointer' }}
			onMouseMove={handleMove}
			onMouseUp={handleClick}
			onClick={onClickHandler}
		>
			{children}
		</Link>
	);
};

export default React.memo(ActiveLink);
