import React from 'react';
import DatePicker, { registerLocale } from 'react-datepicker';
import MaskedInput from 'react-text-mask';

import ru from 'date-fns/locale/ru';
import Input from 'components/UI/Input';

import styles from './LKSettings.module.scss';
import 'react-datepicker/dist/react-datepicker.css';

import Title from 'components/UI/Title';
import Button from 'components/UI/Button';
import Switcher from 'components/UI/Switcher';
//import Image from 'next/image';

//import pencil from 'public/static/img/pencil.svg';
import { useSelector } from 'react-redux';
import { SuisseIntl } from 'helpers/utils/fonts';
import EditUserDataApi from 'api/auth/EditDataUserApi';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { getUserData } from 'store/user/UserDataStore';
import RequestToChangePhoneApi from 'api/auth/RequestToChangePhoneApi';
import ChangePhoneApi from 'api/auth/ChangePhoneApi';

registerLocale('ru', ru);

const editUserDataApi = new EditUserDataApi();
const requestToChangePhoneApi = new RequestToChangePhoneApi();
const changePhoneApi = new ChangePhoneApi();

type LKSettingsTypes = {
	name: string;
	secondName: string;
	birthday: string;
	city: string;
	subscribeNewsletter: boolean;
	email: string;
	phone: string;
	closeSettings: () => void;
	openResetPass: () => void;
};

const LKSettings = ({
	name,
	secondName,
	birthday,
	city,
	subscribeNewsletter,
	email,
	phone,
	closeSettings,
	openResetPass,
}: LKSettingsTypes) => {
	const birthdayArr = birthday.split('-');
	const [birthdayState, setBirthdayState] = React.useState<Date | ''>(
		birthday ? new Date(+birthdayArr[2], +birthdayArr[1] - 1, +birthdayArr[0]) : ''
	);
	const [nameState, setNameState] = React.useState<string>(name ? name : '');
	const [secondNameState, setSecondNameState] = React.useState<string>(secondName ? secondName : '');
	const [cityState, setCityState] = React.useState<string>(city ? city : '');
	const [emailState, setEmailState] = React.useState<string>(email ? email : '');
	const [phoneState, setPhoneState] = React.useState<string>(phone ? phone : '');
	const [isSubscribe, setIsSubscribe] = React.useState<boolean>(subscribeNewsletter ? subscribeNewsletter : false);
	const [isErrorName, setIsErrorName] = React.useState<boolean>(false);
	const [isErrorBirthday, setIsErrorBirthday] = React.useState<boolean>(false);
	const [isErrorSecondName, setIsErrorSecondName] = React.useState<boolean>(false);
	const [isErrorCity, setIsErrorCity] = React.useState<boolean>(false);
	const [isErrorPhone, setIsErrorPhone] = React.useState<boolean>(false);
	const [isErrorEmail, setIsErrorEmail] = React.useState<boolean>(false);
	const [errorText, setErrorText] = React.useState<string>('');
	const [datepickerIsReadonly, setDatepickerIsReadonly] = React.useState<boolean>(birthday !== '');
	//const [phoneIsReadonly, setPhoneIsReadonly] = React.useState<boolean>(phone !== '');
	const [settingsIsSend, setSettingsIsSend] = React.useState<boolean>(false);
	const [smsTimer, setSmsTimer] = React.useState<number | null>(null);
	const [smsCode, setSmsCode] = React.useState<string>('');
	const [isSmsCodeError, setIsSmsCodeError] = React.useState<boolean>(false);
	const [smsIsSended, setSmsIsSended] = React.useState<boolean>(true);

	const interval = React.useRef(null);

	const phoneRef = React.useRef<any | null>(null);
	const datepickerRef = React.useRef<any | null>(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const token = useSelector((state: any) => state.userData.token);

	const dispatch = useAppDispatch();

	const setData = (date: Date) => {
		if (datepickerIsReadonly) return;
		setIsErrorBirthday(false);
		setBirthdayState(date);
	};

	const onChangeInputs = (
		event: React.ChangeEvent<HTMLInputElement>,
		type: 'name' | 'secondName' | 'city' | 'email' | 'phone' | 'subscribe'
	) => {
		if (type === 'name') {
			setNameState(event.target.value);
			setIsErrorName(false);
		}
		if (type === 'secondName') {
			setSecondNameState(event.target.value);
			setIsErrorSecondName(false);
		}
		if (type === 'city') {
			setCityState(event.target.value);
			setIsErrorCity(false);
		}
		if (type === 'email') {
			setIsErrorEmail(false);
			setEmailState(event.target.value);
		}
		if (type === 'phone') {
			setPhoneState(
				event.target.value[0] === '+'
					? event.target.value[0] + event.target.value.slice(1, event.target.value.length).replace(/[^\d]/g, '')
					: event.target.value.replace(/[^\d]/g, '')
			);
			setIsErrorPhone(false);
		}
		if (type === 'subscribe') {
			setIsSubscribe(!isSubscribe);
		}
	};
	const saveSettings = () => {
		if (settingsIsSend) return;
		if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(emailState)) {
			if (birthdayState === null) {
				setIsErrorBirthday(true);
				setErrorText('  ');
				return;
			}

			const parseDate = birthdayState.toLocaleString().replace(/\//g, '.').split(',')[0].split('.');
			setSettingsIsSend(true);
			editUserDataApi
				.editDataUser({
					firstName: nameState,
					lastName: secondNameState,
					birthday: `${parseDate[0]}-${parseDate[1]}-${parseDate[2]}`,
					city: cityState,
					email: emailState,
					subscribeNewsletter: isSubscribe,
				})
				.then(res => {
					if (res.data.errors) {
						setSettingsIsSend(false);
						if (res.data.errors.firstName) {
							setIsErrorName(true);
							setErrorText(res.data.errors.firstName[0]);
							return;
						}
						if (res.data.errors.lastName) {
							setIsErrorSecondName(true);
							setErrorText(res.data.errors.lastName[0]);
							return;
						}
						if (res.data.errors.birthday) {
							setIsErrorBirthday(true);
							setErrorText(res.data.errors.birthday[0]);
							return;
						}
						if (res.data.errors.email) {
							setIsErrorEmail(true);
							setErrorText(res.data.errors.email[0]);
							return;
						}
						if (res.data.errors.city) {
							setIsErrorCity(true);
							setErrorText(res.data.errors.city[0]);
							return;
						}
					} else if (res.data.message === 'ok') {
						dispatch(getUserData({ token })).then(() => {
							closeSettings();
						});
					}
				});
		} else {
			setIsErrorEmail(true);
			setErrorText('  email');
		}
	};

	//const editDatepickerHandler = () => {
	//	setDatepickerIsReadonly(!datepickerIsReadonly);
	//	if (!datepickerIsReadonly) {
	//		if (datepickerRef.current) datepickerRef.current.input.inputElement.blur();
	//	} else {
	//		if (datepickerRef.current) datepickerRef.current.input.inputElement.focus();
	//	}
	//};

	//const editPhoneHandler = () => {
	//	setPhoneIsReadonly(!phoneIsReadonly);
	//	if (!phoneIsReadonly) {
	//		if (phoneRef.current) phoneRef.current.inputElement.blur();
	//	} else {
	//		if (phoneRef.current) phoneRef.current.inputElement.focus();
	//	}
	//};

	const startSmsTimer = () => {
		if (!smsIsSended) return;
		setSmsIsSended(false);
		requestToChangePhoneApi.getRequestToChangePhone({ phone: phoneState }).then(res => {
			setSmsIsSended(true);
			if (res.data.message === 'ok') {
				setSmsTimer(60);
				interval.current = setInterval(() => {
					setSmsTimer(prev => prev - 1);
				}, 1000);
			} else if (res.data.errors && res.data.errors.phone) {
				setIsErrorPhone(true);
				setErrorText(res.data.errors.phone[0]);
			}
		});
	};

	const onChangeSmsCode = (event: React.ChangeEvent<HTMLInputElement>) => {
		setIsSmsCodeError(false);
		setSmsCode(event.target.value);
	};

	const sendCode = () => {
		changePhoneApi.getChangePhone({ phone: phoneState, code: smsCode }).then(res => {
			if (res.data.errors && res.data.errors.phone) {
				setIsErrorPhone(true);
				setErrorText(res.data.errors.phone[0]);
			} else if (res.data.errors && res.data.errors.code) {
				setIsSmsCodeError(true);
				setErrorText(res.data.errors.code[0]);
			} else if (res.data.message === 'ok') {
				dispatch(getUserData({ token })).then(() => {
					setSmsCode('');
					setSmsTimer(null);
					setIsErrorPhone(false);
				});
			}
		});
	};

	React.useEffect(() => {
		if (smsTimer === 0 || smsTimer === null) clearInterval(interval.current);
	}, [smsTimer, interval]);

	return (
		<>
			<Title
				className={styles.title}
				text=" "
				size={isMobile ? 22 : 28}
				marginBottom={isMobile ? 32 : 40}
				withSuisse
			/>
			<Input
				className={`${styles.input} ${isErrorName ? styles.isError : ''}`}
				placeholder=""
				value={nameState}
				type="text"
				isError={isErrorName}
				errorText={errorText}
				//readonly={name !== ''}
				onChange={event => onChangeInputs(event, 'name')}
			/>
			<Input
				className={`${styles.input} ${isErrorSecondName ? styles.isError : ''}`}
				placeholder=""
				value={secondNameState}
				type="text"
				isError={isErrorSecondName}
				errorText={errorText}
				//readonly={secondName !== ''}
				onChange={event => onChangeInputs(event, 'secondName')}
			/>
			<div className={styles.datepickerBlock}>
				<DatePicker
					ref={datepickerRef}
					className={`${styles.input} ${isErrorEmail ? styles.isError : ''}`}
					placeholderText=" "
					dateFormat="dd-MM-yyyy"
					locale="ru"
					selected={birthdayState}
					//readOnly={datepickerIsReadonly}
					onChange={setData}
					customInput={
						<MaskedInput
							className={styles.datepicker}
							//readOnly={datepickerIsReadonly}
							mask={[/[0-3]/, /\d/, '-', /[0-1]/, /\d/, '-', /[1-2]/, /\d/, /\d/, /\d/]}
							guide
							id="inputMask"
							placeholder=" "
							onChange={setData}
						/>
					}
				/>
				{/*{birthday && (
					<Image
						alt="edit"
						src={pencil}
						width={20}
						height={20}
						onClick={editDatepickerHandler}
						style={{
							maxWidth: '100%',
							height: 'auto',
						}}
					/>
				)}*/}
				{isErrorBirthday && <p className={styles.errorText}>{errorText}</p>}
			</div>
			<Input
				className={`${styles.input} ${isErrorCity ? styles.isError : ''}`}
				placeholder=""
				value={cityState}
				type="text"
				isError={isErrorCity}
				errorText={errorText}
				//readonly={city !== ''}
				onChange={event => onChangeInputs(event, 'city')}
			/>
			<Input
				className={`${styles.input} ${isErrorEmail ? styles.isError : ''}`}
				isError={isErrorEmail}
				errorText={errorText}
				placeholder="Email"
				value={emailState}
				type="email"
				//readonly={email !== ''}
				onChange={event => onChangeInputs(event, 'email')}
			/>
			<div className={styles.phoneContainer}>
				{localStorage.getItem('location') === 'RU' ? (
					<div className={styles.phoneBlock}>
						<MaskedInput
							ref={phoneRef}
							mask={[
								'+',
								'7',
								' ',
								/[1-6]|[8-9]/,
								/\d/,
								/\d/,
								' ',
								/\d/,
								/\d/,
								/\d/,
								' ',
								/\d/,
								/\d/,
								' ',
								/\d/,
								/\d/,
							]}
							guide
							id="inputMask"
							placeholder="+7 999 000 00 00"
							type="tel"
							className={`${styles.input} ${styles.phone} ${smsTimer !== null ? styles.phoneTimer : ''} ${
								isErrorPhone ? styles.errorPhone : ''
							}`}
							value={phoneState}
							//readOnly={phoneIsReadonly}
							onChange={event => onChangeInputs(event, 'phone')}
						/>
						{/*{phone && (
							<Image
								alt="edit"
								src={pencil}
								width={20}
								height={20}
								onClick={editPhoneHandler}
								style={{
									maxWidth: '100%',
									height: 'auto',
								}}
							/>
						)}*/}
						{phoneState.length >= 11 && smsTimer === null && (
							<p style={phone ? { right: '30px' } : {}} onClick={startSmsTimer}>
								 
							</p>
						)}
						{phoneState.length >= 11 && smsTimer !== null && smsTimer !== 0 && (
							<p style={phone ? { right: '30px' } : {}}>{`00:${smsTimer}`}</p>
						)}
						{phoneState.length >= 11 && smsTimer !== null && smsTimer === 0 && (
							<p style={phone ? { right: '30px' } : {}} onClick={startSmsTimer}>
								  
							</p>
						)}
					</div>
				) : (
					<div className={styles.phoneBlock}>
						<Input
							className={`${styles.input} ${isErrorPhone ? styles.isError : ''} ${
								smsTimer !== null ? styles.phoneTimer : ''
							}`}
							placeholder="+7 999 000 00 00"
							value={phoneState}
							type="tel"
							isError={isErrorPhone}
							errorText={errorText}
							//readonly={phoneIsReadonly}
							onChange={event => onChangeInputs(event, 'phone')}
						/>
					</div>
				)}
				{isErrorPhone && errorText && localStorage.getItem('location') === 'RU' && (
					<p
						className={`${styles.errorText} ${styles.errorTextPhone} ${
							smsTimer !== null ? styles.errorTextPhoneWithCode : ''
						}`}
					>
						{errorText}
					</p>
				)}
			</div>
			{smsTimer !== null && (
				<div className={styles.phoneBlock}>
					<Input
						className={`${styles.smsInput} ${isSmsCodeError ? styles.isErrorSmsCode : ''}`}
						value={smsCode}
						placeholder=""
						type="number"
						errorText={errorText}
						isError={isSmsCodeError}
						onChange={onChangeSmsCode}
					/>
					{smsCode.length >= 6 && (
						<p className={styles.codeField} onClick={sendCode}>
							 
						</p>
					)}
				</div>
			)}
			<Button
				className={`${styles.button} ${styles.firstBtn}`}
				text=" "
				isInvisible
				isActive
				disabled={settingsIsSend}
				onClick={saveSettings}
			/>
			<Button className={styles.button} text=" " isInvisible onClick={openResetPass} />
			<Switcher
				className={styles.switcher}
				value={isSubscribe}
				label="  "
				onChange={event => onChangeInputs(event, 'subscribe')}
			/>
			<button onClick={closeSettings} className={`${styles.back} ${SuisseIntl.className}`}>
				
			</button>
		</>
	);
};

export default React.memo(LKSettings);
