import React from 'react';

import styles from './Switcher.module.scss';

type SwitcherTypes = {
	value: boolean;
	label: string;
	className?: string;
	onChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
};

const Switcher = ({ value, label, className, onChange }: SwitcherTypes) => {
	return (
		<div className={`${styles.switch} ${className}`}>
			<p>{label}</p>
			<input type="checkbox" id="switcher" defaultChecked={value} onChange={onChange} />
			<label htmlFor="switcher" className={`${styles.slider} ${styles.round}`}></label>
		</div>
	);
};

export default React.memo(Switcher);
