import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, AddReviewParams, AddReviewResp, IAddReviewApi, AddReviewData } from './types';

export default class AddReviewApi implements IAddReviewApi {
	private readonly _apiStore = RootStore.apiStore;

	async addReview(params: AddReviewParams, data: AddReviewData): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<AddReviewResp, AddReviewResp>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/order/${params.id}/comment`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
