import Button from 'components/UI/Button';
import Title from 'components/UI/Title';
import React from 'react';
import LKCheckbox from '../LKCheckbox';
import Input from 'components/UI/Input';
import MaskedInput from 'react-text-mask';

import styles from './SignIn.module.scss';

import ActiveLink from 'components/ActiveLink';
import { useRouter } from 'next/router';
import routes from 'helpers/routes';
import { useSelector } from 'react-redux';
import { SuisseIntl } from 'helpers/utils/fonts';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { getTokenWithEmail, getTokenWithPhone, getUserData } from 'store/user/UserDataStore';
import { ApiResponse } from 'api/RootApi/ApiStore/types';
import { SignInModel } from 'api/auth/models/SignInModel';
import SmsConfirationApi from 'api/auth/SmsConfirationApi';
import { getCheckoutProduct } from 'store/product/CheckoutProductStore';
import RequestToRestoreApi from 'api/auth/RequestToRestoreApi';
import { getFavoritesIds } from 'store/favorites/FavoritesStore';

const smsConfirationApi = new SmsConfirationApi();
const requestToRestoreApi = new RequestToRestoreApi();

const SignIn = () => {
	const [email, setEmail] = React.useState<string>('');
	const [password, setPassword] = React.useState<string>('');
	const [phone, setPhone] = React.useState<string>('');
	const [smsCode, setSmsCode] = React.useState<string>('');
	const [isRememberMe, setIsRememberMe] = React.useState<boolean>(false);
	const [isPasswordError, setIsPasswordError] = React.useState<boolean>(false);
	const [isEmailError, setIsEmailError] = React.useState<boolean>(false);
	const [isPhoneError, setIsPhoneError] = React.useState<boolean>(false);
	const [isSmsCodeError, setIsSmsCodeError] = React.useState<boolean>(false);
	const [errorText, setErrorText] = React.useState<string>('');
	const [isResetPass, setIsResetPass] = React.useState<boolean>(false);
	const [isSuccessResetPass, setIsSuccessResetPass] = React.useState<boolean>(false);
	const [signMethod, setSignMethod] = React.useState<string>('mail');
	const [smsTimer, setSmsTimer] = React.useState<number | null>(null);
	const [smsIsSended, setSmsIsSended] = React.useState<boolean>(true);
	const [signInRequestIsSend, setSignInRequestIsSend] = React.useState<boolean>(false);
	const [resetPassRequestIsSend, setResetPassRequestIsSend] = React.useState<boolean>(false);

	const interval = React.useRef(null);

	const dispatch = useAppDispatch();

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const router = useRouter();

	const onChangeLKInputs = (event: React.ChangeEvent<HTMLInputElement>, isEmail) => {
		if (isEmail) {
			setEmail(event.target.value);
			setIsEmailError(false);
		} else {
			setIsPasswordError(false);
			setPassword(event.target.value);
		}
	};

	const onRememberMeHandler = () => {
		setIsRememberMe(!isRememberMe);
	};

	const onSignHandler = () => {
		if (signInRequestIsSend) return;
		if (signMethod === 'mail') {
			if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email) && password !== '') {
				setIsPasswordError(false);
				setIsEmailError(true);
				setErrorText('  email');
				return;
			}
			if (password === '' && email) {
				setIsPasswordError(true);
				setIsEmailError(false);
				setErrorText(' ');
				return;
			}
			setSignInRequestIsSend(true);
			dispatch(getTokenWithEmail({ email, password, rememberMe: isRememberMe })).then(res => {
				const { data } = res.payload as ApiResponse<SignInModel, SignInModel>;
				if (data.errors) {
					if (Array.isArray(data.errors.password)) {
						setIsPasswordError(true);
						setIsEmailError(false);
						setErrorText(data.errors.password[0]);
					}
					if (Array.isArray(data.errors.email)) {
						setIsPasswordError(false);
						setIsEmailError(true);
						setErrorText(data.errors.email[0]);
					}
					setSignInRequestIsSend(false);
				}
				if (data.token) {
					localStorage.setItem('token', data.token);
					dispatch(getUserData({ token: data.token })).then(() => {
						if (sessionStorage.getItem('authRedirect')) {
							sessionStorage.removeItem('authRedirect');
							router.back();
						} else {
							if (router.asPath.includes('/checkout?personal-area=sign-in&isOrder'))
								return router.push('/checkout');
							router.push(router.asPath.split('?')[0] + routes.lk, undefined, { shallow: true });
						}
						dispatch(getCheckoutProduct({ token: localStorage.getItem('token') }));
						dispatch(getFavoritesIds({ token: localStorage.getItem('token') }));
					});
				}
			});
		} else {
			setSignInRequestIsSend(true);
			dispatch(getTokenWithPhone({ phone, code: smsCode, rememberMe: isRememberMe })).then(res => {
				const { data } = res.payload as ApiResponse<SignInModel, SignInModel>;
				if (data.errors) {
					if (Array.isArray(data.errors.phone)) {
						setIsPhoneError(true);
						setIsSmsCodeError(false);
						setErrorText(data.errors.phone[0]);
					}
					if (Array.isArray(data.errors.code)) {
						setIsPhoneError(false);
						setIsSmsCodeError(true);
						setErrorText(data.errors.code[0]);
					}
					setSignInRequestIsSend(false);
				}
				if (data.token) {
					localStorage.setItem('token', data.token);
					dispatch(getUserData({ token: data.token })).then(() => {
						if (sessionStorage.getItem('authRedirect')) {
							sessionStorage.removeItem('authRedirect');
							router.back();
						} else {
							if (router.asPath.includes('/checkout?personal-area=sign-in&isOrder'))
								return router.push('/checkout');
							router.push(router.asPath.split('?')[0] + routes.lk, undefined, { shallow: true });
						}
						dispatch(getCheckoutProduct({ token: localStorage.getItem('token') }));
						dispatch(getFavoritesIds({ token: localStorage.getItem('token') }));
					});
				}
			});
		}
	};

	const onOpenResetPass = () => {
		setIsResetPass(!isResetPass);
	};

	const onResetPasswordSubmit = () => {
		if (!/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)) {
			setIsEmailError(true);
			setErrorText('  email');
		} else {
			setResetPassRequestIsSend(true);
			requestToRestoreApi.getRequestToRestore({ email }).then(res => {
				setResetPassRequestIsSend(false);
				if (res.data.errors?.email) {
					setIsEmailError(true);
					setErrorText(res.data.errors.email[0]);
				}
				if (res.data.message === 'ok') {
					setIsSuccessResetPass(true);
				}
			});
		}
	};

	const onMethodSignHendler = isEmail => {
		if (isEmail) {
			setSignMethod('mail');
		} else {
			setSignMethod('phone');
		}
	};

	const onChangePhoneHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
		setPhone(
			event.target.value[0] === '+'
				? event.target.value[0] + event.target.value.slice(1, event.target.value.length).replace(/[^\d]/g, '')
				: event.target.value.replace(/[^\d]/g, '')
		);
		setIsPhoneError(false);
		setIsSmsCodeError(false);
	};

	const onChangeSmsCode = (event: React.ChangeEvent<HTMLInputElement>) => {
		setIsSmsCodeError(false);
		setSmsCode(event.target.value);
	};

	const startSmsTimer = () => {
		setIsPhoneError(false);
		if (!smsIsSended) return;
		setSmsIsSended(false);
		smsConfirationApi.getSmsConfiration({ phone }).then(res => {
			setSmsIsSended(true);
			if (res.data.message === 'ok') {
				setSmsTimer(60);
				interval.current = setInterval(() => {
					setSmsTimer(prev => prev - 1);
				}, 1000);
			} else if (res.data.errors && res.data.errors.phone) {
				setIsPhoneError(true);
				setErrorText(res.data.errors.phone[0]);
			}
		});
	};

	React.useEffect(() => {
		if (smsTimer === 0) clearInterval(interval.current);
	}, [smsTimer, interval]);

	if (isResetPass) {
		return (
			<>
				<Title text=" " size={isMobile ? 22 : 28} marginBottom={isMobile ? 24 : 40} withSuisse />
				<button className={`${styles.back} ${SuisseIntl.className}`} onClick={onOpenResetPass}>
					
				</button>
				{isSuccessResetPass ? (
					<p className={styles.description}>   &nbsp;   !</p>
				) : (
					<>
						<p className={styles.description}>
							 &nbsp;  ,    , &nbsp;&nbsp; 
							  &nbsp; &nbsp; 
						</p>
						<Input
							className={`${styles.resetInput} ${isEmailError ? styles.isError + ' ' + styles.isErrorRestore : ''}`}
							value={email}
							placeholder="Email"
							type="email"
							errorText={errorText}
							isError={isEmailError}
							onChange={event => onChangeLKInputs(event, true)}
						/>
						<Button
							className={styles.resetButton}
							onClick={onResetPasswordSubmit}
							isActive
							isInvisible
							disabled={resetPassRequestIsSend}
							text=" "
						/>
					</>
				)}
			</>
		);
	}

	return (
		<>
			<Title
				text={signMethod === 'mail' ? '  ' : '  '}
				size={isMobile ? 22 : 28}
				marginBottom={isMobile ? 32 : 40}
				withSuisse
			/>
			{/*{localStorage.getItem('location') === 'RU' && (*/}
			<div className={styles.buttonsBlock}>
				<Button
					className={signMethod === 'phone' ? styles.buttonOpasity : ''}
					isInvisible
					text=" "
					onClick={() => onMethodSignHendler(true)}
				/>
				<Button
					className={signMethod === 'mail' ? styles.buttonOpasity : ''}
					isInvisible
					text=" "
					onClick={() => onMethodSignHendler(false)}
				/>
			</div>
			{/*)}*/}
			{signMethod === 'mail' ? (
				<>
					<div className={styles.inputBlock}>
						<Input
							value={email}
							placeholder="Email"
							type="email"
							errorText={isEmailError && isPasswordError ? '' : errorText}
							isError={isEmailError}
							className={isEmailError ? styles.isError : ''}
							onChange={event => onChangeLKInputs(event, true)}
						/>
						<Input
							value={password}
							placeholder=""
							type="password"
							errorText={errorText}
							isError={isPasswordError}
							onChange={event => onChangeLKInputs(event, false)}
						/>
					</div>
					<div className={styles.actions}>
						<LKCheckbox value={isRememberMe} id="RememberMe" onChange={onRememberMeHandler} label=" " />
						<p onClick={onOpenResetPass}> ?</p>
					</div>
					<div className={styles.signButtonBlock}>
						<Button onClick={onSignHandler} isActive isInvisible disabled={signInRequestIsSend} text="" />
						<ActiveLink href={router.asPath.split('?')[0] + routes.registration}>
							<Button isInvisible text="" />
						</ActiveLink>
					</div>
				</>
			) : (
				<>
					<div className={styles.inputBlock}>
						<div className={styles.phoneBlock}>
							<MaskedInput
								className={`${styles.inputMask}  ${isPhoneError ? styles.errorPhone : ''}`}
								mask={[
									'+',
									'7',
									' ',
									/[1-6]|[8-9]/,
									/\d/,
									/\d/,
									' ',
									/\d/,
									/\d/,
									/\d/,
									' ',
									/\d/,
									/\d/,
									' ',
									/\d/,
									/\d/,
								]}
								guide
								id="inputMask"
								type="tel"
								placeholder="+7 999 000 00 00"
								onChange={onChangePhoneHandler}
								value={phone}
							/>
							{phone.length === 12 && smsTimer === null && <p onClick={startSmsTimer}> </p>}
							{phone.length === 12 && smsTimer !== null && smsTimer !== 0 && <p>{`00:${smsTimer}`}</p>}
							{phone.length === 12 && smsTimer !== null && smsTimer === 0 && (
								<p onClick={startSmsTimer}>  </p>
							)}
						</div>
						{isPhoneError && errorText && <p className={styles.errorText}>{errorText}</p>}
						<Input
							value={smsCode}
							placeholder=""
							type="number"
							errorText={errorText}
							isError={isSmsCodeError}
							onChange={onChangeSmsCode}
						/>
					</div>
					<div className={styles.actions}>
						<LKCheckbox value={isRememberMe} id="RememberMe" onChange={onRememberMeHandler} label=" " />
					</div>
					<div className={styles.signButtonBlock}>
						<Button onClick={onSignHandler} isActive isInvisible disabled={signInRequestIsSend} text="" />
						<ActiveLink href={router.asPath.split('?')[0] + routes.registration}>
							<Button isInvisible text="" />
						</ActiveLink>
					</div>
				</>
			)}
		</>
	);
};

export default React.memo(SignIn);
