import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';
import { EditDataUserApi, EditDataUserModel, normolizeEditDataUser } from '../models/EditDataUserModel';

import { ApiResp, EditDataUserApiData, IEditDataUserApi } from '../EditDataUserApi/types';

export default class EditUserDataApi implements IEditDataUserApi {
	private readonly _apiStore = RootStore.apiStore;

	async editDataUser(data: EditDataUserApiData): Promise<ApiResp<EditDataUserModel>> {
		const response = await this._apiStore.request<ApiResponse<EditDataUserApi, EditDataUserApi>>({
			method: HTTPMethod.PUT,
			endpoint: `/api/v1/me`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			response.data = normolizeEditDataUser(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
