import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import {
	ApiResp,
	RequestToRestoreApiData,
	IRequestToRestoreApi,
	RequestToRestoreResponseData,
} from '../RequestToRestoreApi/types';

export default class RequestToRestoreApi implements IRequestToRestoreApi {
	private readonly _apiStore = RootStore.apiStore;

	async getRequestToRestore(data: RequestToRestoreApiData): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<RequestToRestoreResponseData, RequestToRestoreResponseData>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/auth/request-to-restore`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
