import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, smsConfirationApiData, ISmsConfirationApi, smsConfirationResponseData } from '../SmsConfirationApi/types';

export default class SmsConfirationApi implements ISmsConfirationApi {
	private readonly _apiStore = RootStore.apiStore;

	async getSmsConfiration(data: smsConfirationApiData, isSignUp?: boolean): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<smsConfirationResponseData, smsConfirationResponseData>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/auth/${isSignUp ? 'sign-up' : 'sign-in'}/sms`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data,
		});
		try {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
