import ActiveLink from 'components/ActiveLink';
import Button from 'components/UI/Button';
import Title from 'components/UI/Title';

import routes from 'helpers/routes';
import React from 'react';
import ProductItem from 'components/ProductItem';

import styles from './Favorites.module.scss';
import { useSelector } from 'react-redux';
import { useRouter } from 'next/router';
import { SuisseIntl } from 'helpers/utils/fonts';
import { getFavoritesIds, getFavoritesList } from 'store/favorites/FavoritesStore';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import FavoritesApi from 'api/favorites/FavoritesApi';
import AddProductApi from 'api/checkout/AddProductApi';
import SubTitle from 'components/UI/SubTitle';

const favoritesApi = new FavoritesApi();
const addProductApi = new AddProductApi();

const Favorites = () => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const favoritesList = useSelector((state: any) => state.favorites.favoritesList);
	const token = useSelector((state: any) => state.userData.token);

	const router = useRouter();

	const dispatch = useAppDispatch();

	const closeClickHandler = (id: number) => {
		favoritesApi.deleteFavorites({ id }).then(() => {
			dispatch(getFavoritesList({ token: localStorage.getItem('token') || null }));
			dispatch(getFavoritesIds({ token: localStorage.getItem('token') || null }));
		});
	};

	const addInCheckoutHandler = (id: number) => {
		return addProductApi.addProduct({ id });
	};

	React.useEffect(() => {
		dispatch(getFavoritesList({ token: localStorage.getItem('token') || null }));
	}, []);

	return (
		<>
			<Title text="" size={isMobile ? 22 : 28} marginBottom={isMobile ? 32 : 40} withSuisse />
			<div className={styles.favorites}>
				{Array.isArray(favoritesList) && favoritesList.length === 0 && (
					<SubTitle text="   " size={16} marginTop={0} withSuisse />
				)}
				{favoritesList.map(item => {
					return (
						<ProductItem
							key={item.id}
							data={item}
							closeClickHandler={() => closeClickHandler(item.id)}
							addInCheckoutHandler={() => addInCheckoutHandler(item.id)}
						/>
					);
				})}
			</div>
			<ActiveLink className={styles.button} href={routes.checkout}>
				<Button
					style={router.query.from === 'lk' ? { marginBottom: '40px' } : {}}
					text="  "
					isInvisible
					isActive={isMobile}
				/>
			</ActiveLink>
			{router.query.from === 'lk' && (
				<ActiveLink href={token ? router.asPath.split('?')[0] + routes.lk : router.asPath.split('?')[0] + routes.signIn}>
					<button className={`${styles.back} ${SuisseIntl.className}`}></button>
				</ActiveLink>
			)}
		</>
	);
};

export default React.memo(Favorites);
